<template>
<section>
  <!--工具条-->
  <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
    <el-form :inline="true">
      <el-form-item>
        <span> <i class="el-icon-menu" style="margin-right:3px;"></i>产品类别</span>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleAdd">+ 添加类别</el-button>
      </el-form-item>
      <el-form-item style="float: right;">
        产品类别共：<span v-text="total"></span>个
      </el-form-item>
    </el-form>
  </el-col>

  <!--列表-->
  <el-table :data="category_id" highlight-current-row v-loading="listLoading" style="width: 100%;" height="500">
    <el-table-column label="编号" width="50">
      <template slot-scope="scope">
        <span v-text="Number(scope.$index) + 1 + (page - 1) * 10"></span>
      </template>
    </el-table-column>
    </el-table-column>
    <el-table-column prop="name" label="产品类别">
    </el-table-column>
    <el-table-column label="排序操作" width="130" fixed="right">

      <template slot-scope="scope">
        <el-button size="medium" type='text' @click.stop="sorttop(scope.$index, scope.row)">至顶 </el-button>
        <el-button size="medium" type='text' @click.stop="sortUp(scope.$index, scope.row)">↑ </el-button>
        <el-button size="medium" type='text' @click.stop="sortDown(scope.$index, scope.row)">↓</el-button>
      </template>

    </el-table-column>
    <el-table-column label="操作" width="150" fixed="right">
      <template slot-scope="scope">
        <el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  <!--工具条-->
  <el-col :span="24" class="toolbar">
    <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
    </el-pagination>
    </el-pagination>
  </el-col>

  <!--新增界面-->
  <el-dialog title="添加产品类别" style="width: 50%;margin: 0 auto;" v-model="addFormVisible" @close="cancel">
    <el-form :model="addForm" label-width="10px" :rules="addFormRules" ref="addForm">
      <el-form-item prop="name">
        <el-input v-model="addForm.name" auto-complete="off" :maxlength="10" placeholder="请输入产品类别（10字以内）"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click.native="cancel">取消</el-button>
      <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
    </div>
  </el-dialog>
</section>
</template>

<script>
import Vue from 'vue'
import util from '../../common/js/util'
import {
  getProtypeList,
  removeProtype,
  batchRemoveProtype,
  addProtype,
  isToTopType,
  isToUpType
} from '../../api/api';

export default {
  data() {
    return {
      category_id: [],
      total: 0,
      page: 1,
      listLoading: false,
      addFormVisible: false, //新增界面是否显示
      addLoading: false,
      addFormRules: {
        name: [{
          required: true,
          message: '请输入商品名称（10字以内）',
          trigger: 'blur'
        }],
      },
      //新增界面数据
      addForm: {
        name: ''
      }
    }
  },
  methods: {
    // 上移按钮
    sortUp(index, row) {
      if (index === 0) {
        this.$message({
          message: '已经是列表中第一个素材！',
          type: 'warning'
        })
      } else {
		isToUpType ({
		  above_category_id: this.category_id[index - 1].category_id,
          under_category_id: this.category_id[index].category_id
        }).then((res) => {
          if (res.data.code === 0) {
            this.getProtype()
          }
        })
      }
    },

    // 至顶按钮
    sorttop(index, row) {
      if (index === 0) {
        this.$message({
          message: '已经是列表中第一个素材！',
          type: 'warning'
        })
      } else {
        isToTopType ({
		    category_id: row.category_id,
          is_top: 1
        }).then((res) => {
          if (res.data.code === 0) {
            this.getProtype()
          }
        })
      }
    },
    // 下移按钮
    sortDown(index, row) {
      if (index === (this.category_id.length - 1)) {
        this.$message({
          message: '已经是列表中最后一个素材！',
          type: 'warning'
        })
      } else {
        isToUpType ({
		  above_category_id: this.category_id[index].category_id,
          under_category_id: this.category_id[index + 1].category_id
        }).then((res) => {
          if (res.data.code === 0) {
            this.getProtype()
          }
        })
      }
    },

    //获取产品类别列表
    getProtype() {
      let para = {
        page: this.page,
        size: 10
      };
      this.listLoading = true;
      getProtypeList(para).then((res) => {
        this.total = res.data.total;
        this.category_id = res.data.data;
        this.listLoading = false;
      });
    },
    //pageNum变化事件
    handleCurrentChange(val) {
      this.page = val
      this.getProtype();
    },
    //显示编辑界面
    handleEdit: function(index, row) {
      this.addFormVisible = true;
      this.addForm = Object.assign({}, row);
    },
    //显示新增界面
    handleAdd: function() {
      this.addFormVisible = true;
    },
    cancel() {
      this.addFormVisible = false;
      this.resetForm()
    },
    // 提交
    addSubmit: function() {
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            this.addLoading = true;
            addProtype(this.addForm).then((res) => {
              this.addLoading = false;
              this.$message({
                message: '提交成功',
                type: 'success'
              });
              this.$refs['addForm'].resetFields();
              this.addFormVisible = false;
              this.getProtype();
            });
          });
        }
      });
    },
    resetForm() {
      this.addForm = {
        name: ''
      }
    },
    //删除
    handleDel: function(index, row) {
      this.$confirm('确认删除该记录吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true;
        let para = {
          category_ids: row.category_id
        };
        removeProtype(para).then((res) => {
          this.listLoading = false;
          this.$message({
            message: '删除成功',
            type: 'success'
          });
          this.getProtype();
        });
      }).catch(() => {});
    }
  },
  mounted() {
    this.getProtype();
  }
}
</script>

<style scoped>
.l-style {
  color: #0082E6;
  margin-right: 5px;
}
</style>



